    flex-direction: column;
    align-items: flex-start; /*  center */
    gap: 12px;
  }
  .hero__photo {
    width: 112px; /*  */
  }
}

@media screen and (max-width: 600px) {

  .skill-wrap{
    margin-top:0px;
  }
.text_name_wrap span.mb-40{
  margin-bottom:10px;
}
}

.bullets li strong {
  display: block;           /*   */
  color: #FAD54D;
  font-weight: 900;
  margin-bottom: 4px;        /*  */
  line-height: 1.4;
  font-size:15px;
}

/*  */
.bullets li {
  line-height: 1.9;
  margin-bottom: 12px;
  font-size:16px;
  color:#d9d9d9;
}
.pt-10{
  padding-top:10px!important;
}
.span-wrap{
  display:flex;
}

/* Skills Radar Chart */
.skills-radar-container {
  width: 100%;
  padding-top:16px;
}

@media screen and (max-width: 768px) {
  .skills-radar-container {
    height: 350px;
    max-width: 100%;
    padding: 15px;
  }
}

@media screen and (max-width: 500px) {
  .skills-radar-container {
    height: 300px;
    padding: 10px;
  }
}

/* Skills Detail Title */
.skills-detail-title {
  font-size: 18px;
  color: #FAD54D;
  font-weight: 700;
  margin: 30px 0 20px;
  text-align: left;
}

/* Skills Tags */
.skills-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
  margin-bottom: 60px;
}

.skill-tag {
  display: inline-block;
  padding: 8px;
  background: rgba(250, 213, 77, 0.1);
  border-radius: 6px;
  color: #d9d9d9;
  font-size: 15px;
  font-weight: 500;
  transition: all 0.3s ease;
  line-height:24px!important;
}


@media screen and (max-width: 600px) {
  .skills-tags {
    gap: 8px;
  }

  .skill-tag {
    padding: 8px 16px;
    font-size: 14px;
  }
}
</style>



